<template>
  <v-container grid-list-xs>
    <v-row>
      <v-col align="center">
        <v-card
          class="pa-2 mt-5"
          max-width="800"
          color="blue-grey lighten-5"
          outlined
          :elevation="10"
        >
          <v-subheader class="overline">
            <v-icon class="mr-5">fa-shopping-cart</v-icon>Carrito
          </v-subheader>
          <div v-if="in_cart > 0">
            <v-card class="mx-auto mt-5" v-if="decodedList.status === 'LOADED'">
              <v-subheader class="overline" @click="resumen = !resumen">
                <v-icon class="mr-2">{{resumen?'fa-caret-up':'fa-caret-down'}}</v-icon>
                Resumen: {{in_cart}} productos
              </v-subheader>
              <v-expand-transition>
                <div v-if="resumen">
                  <v-list class="my-0 py-0">
                    <template v-for="(producto, i) in decodedList.list">
                      <v-list-item :key="producto._id" @click="openProduct(producto._id)" class="py-0">
                        <v-list-item-avatar tile>
                          <v-img :src="getIMG(producto.img)"></v-img>
                        </v-list-item-avatar>
                        <v-list-item-content>
                          <v-list-item-title
                            class="font-weight-regular text-left"
                            v-html="producto.descripcion"
                          />
                          <v-list-item-subtitle
                            class="font-weight-medium text-left"
                            v-html="'CANTIDAD: ' +getCantidad(producto._id)"
                          />
                          <v-list-item-subtitle
                            class="font-weight-medium text-left"
                            v-html="'PRECIO: ' + getPrecio(producto._id, getCantidad(producto._id))"
                          />
                        </v-list-item-content>
                      </v-list-item>
                      <v-divider :key="i + 'd'" />
                    </template>
                  </v-list>
                </div>
              </v-expand-transition>
            </v-card>

            <v-subheader
              class="mt-3 pl-2 text-md-body-1 text-uppercase font-weight-medium"
            >total: {{formatMoney(total)}}</v-subheader>
              <v-card-actions>
                <v-btn color="blue-grey lighten-1" dark>comprar Carrito</v-btn>
                <v-btn color="grey darken-1" outlined>Vaciar Carrito</v-btn>
              </v-card-actions>
          </div>
            <div v-else>
              <v-card-text class="pt-0">
                  <v-subheader class="pt-0">No hay produtos que mostrar</v-subheader>
                  <v-btn color="success" text @click="$router.push('/')">agregar productos</v-btn>
              </v-card-text>
            </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({
    resumen: true
  }),
  methods: {
    getIMG(id) {
      return require(`@/assets/products/${id}`);
    },
    getCantidad(id){
      return this.cart.find(e => e.producto === id).cantidad
    },
    getPrecio(id, cantidad){
      return this.formatMoney(this.cart.find(e => e.producto === id).precio * cantidad)
    },
    formatMoney(mount) {
      return (mount).toLocaleString('en-US', {
        style: 'currency',
        currency: 'MXN',
      })
    },
    openProduct(id){
      this.$router.push('/product/' + id)
    }
  },
  computed: {
    ...mapState("shopping_cart", ["cart", "total", "in_cart", "decodedList"])
  },
  async created(){
    setTimeout(async ()=> await this.$store.commit('shopping_cart/decodeCarrito'), 1500)
  }
};
</script>